.contact-section {
  #profile-link {
    margin: 0.5rem;
    display: inline-block;
    box-sizing: border-box;
    border-radius: 50%;
    text-decoration: none;
    background-color: white;

    i {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 7rem;
      height: 7rem;
      font-size: 5rem;
      border-radius: 50%;
      transition-duration: 0.3s;
    }
  }

  #profile-link:hover {
    i {
      box-shadow: 0px 0px 25px 6px rgba(237, 237, 237, 1);
    }

    .fa-codepen {
      border-radius: 50%;
      background-color: #000;
      border-color: #000;
      color: white;
    }

    .fa-github {
      border-radius: 50%;
      background-color: #211f1f;
      color: white;
    }

    .fa-envelope {
      border-radius: 50%;
      background-color: red;
      color: white;
    }

    .fa-linkedin-in {
      border-radius: 50%;
      background-color: #0072b1;
      color: white;
    }
  }

  .fa-codepen {
    color: #000;
  }

  .fa-github {
    color: #211f1f;
  }

  .fa-envelope {
    color: red;
  }

  .fa-linkedin-in {
    color: #0072b1;
  }

  .wrapper {
    opacity: 0;
  }

  @media screen and (max-width: 768px) {
    #profile-link i {
      width: 4rem;
      height: 4rem;
      font-size: 2.5rem;
    }
  }
}
